.mce-content-body [lang] {
	background: #f5ebd0;
	padding: 2px 3px 2px 0;
	margin: -2px -3px -2px 0;
}
.mce-content-body [lang][data-language]::before {
	content: attr(data-language);
	display: inline-block;
	vertical-align: bottom;
	border: 1px solid #bc9323;
	border-radius: 2px;
	padding: 0 2px;
	margin: 0 3px 0 0;
	font-size: 0.6em;
	color: #bc9323;
}
.mce-content-body [lang]:not([data-language])::before {
	content: attr(lang);
	display: inline-block;
	vertical-align: bottom;
	border: 1px solid #bc9323;
	border-radius: 2px;
	padding: 0 2px;
	margin: 0 3px 0 0;
	font-size: 0.6em;
	color: #bc9323;
}
